@import-normalize;
body {  font-family: sans-serif;  line-height: 1.5;}h1,h2,h3,h4,h5,h6,p {  margin: 0;}ul,ol {  margin: 0;  padding: 0; list-style: none;}img {  display: block;  max-width: 100%;  height: auto;}



.profile{
width: 252px;
height: 332px;
display: flex;
margin-left: auto;
margin-right: auto;
flex-direction: column;
align-items: center;
justify-content: space-between;
box-shadow: 0px 0px 4px 1px #000000;
}
.description{
    display: flex;
    flex-direction: column;
align-items: center;
justify-content: space-between;
}
.avatar{
width: 100px;
height: 100px;
border-radius: 50px;
box-shadow: 0px 0px 45px -2px rgba(0,0,0,0.31);
margin: 32px;
display: block;
}
.name{
font-size: 16px;
font-weight: 700;
}
.tag{
    font-size: 10px;
    font-weight: 500;
}
.location{
    font-size: 10px;
    font-weight: 500;
}
.stats{
    width: 100%;
    margin-top: 25px;
    margin-bottom: 25px;
display: flex;
justify-content: space-around;
}
.stats li{
    display: flex;
    flex-direction: column;
    align-items: center;

    }

.label{
    font-size: 16px;
    font-weight: 500;
}
.quantity{
    font-size: 16px;
    font-weight: 700;
}